	module.exports = {"data":{"assignments":{"id":"YXNzaWdubWVudDo2Mjg5","title":"What Is SEO Content? A Guide to Creating Content for SEO | WordStream","description":"<p>Home : Blog : What Is SEO Content? A Guide to Creating Content for SEO</p><p>What Is SEO Content? A Guide to Creating Content for SEO</p><p>Last updated: Feb 28, 2018</p><p>If you’re relatively new to the world of search marketing, you may have heard the term “SEO content” being thrown around in marketing meetings.</p><p>This beginner’s guide is designed to answer three questions:</p><p>What is “SEO content”?</p><p>What types of SEO content are there?</p><p>What is my SEO content strategy?</p><p>If you have any questions about strategies for SEO content creation that I don’t answer here, let me know in the comments and I’ll answer them here or in a future blog post.</p><p>Let’s get started!</p><p>What Is SEO Content?</p><p>To understand what marketers mean by SEO content, it’s helpful to break down the phrase into its component parts:</p><p>“SEO” refers to search engine optimization, or the process of optimizing a website so that people can easily find it via search engines like Google.</p><p>By “content,” we mean any information that lives on the web and can be consumed on the web ( more on the various types of content below ).</p><p>So, putting these two concepts together: SEO content is any content created with the goal of attracting search engine traffic .</p><p>I’m not going to tell you everything you need to know about optimizing your content for search engines here; that’s a whole ‘nother guide.</p>"}},"pathContext":{"id":"YXNzaWdubWVudDo2Mjg5"}}

